.social {
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 2rem 4rem;

  position: fixed;
  bottom: 0;
  z-index: 1;
}

.social__links {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.social__links-container {
  position: fixed;
  right: 4rem;
  display: flex;
  flex-direction: row;
  text-align: left;
}

.social__links-container p{
  color: #000000;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  text-transform: uppercase;
  margin-left: 30px;
  cursor: pointer;
}

.social__links-container a:hover {
  font-weight: bold;
}

@media screen and (max-width: 500px){
  .social__links-container {
    right: 50%;
    padding-right: 30px;
    transform: translate(50%, 0);
  }
}