.nb {
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 2rem 4rem;

  position: fixed;
  top: 0;
  z-index: 1;
}

.nb__links {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.nb__links-logo {
  margin-right: 2rem;
  cursor: pointer;
}

.nb__links-logo img{
  width: 50px;
  height: 50px;
}

.nb__links-container {
  position: fixed;
  right: 4rem;
  display: flex;
  flex-direction: column;
  text-align: left;
  min-width: 70px;
}

.nb__links-container p{
  color: #000000;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  text-transform: lowercase;
  margin: 0;
  cursor: pointer;
}

.nb__links-container a:hover {
  font-weight: bold;
}

.nb__menu {
  position: fixed;
  right: 4rem;
  display: none;
}

.nb__menu svg {
  cursor: pointer;
}

.nb__menu-container {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;

  text-align: end;
  background-color: var(--color-footer);
  padding: 1rem;
  position: absolute;
  top: 20px;
  right: 0;
  margin-top: 1rem;
  min-width: 100px;
  border-radius: 5px;
  box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}

.nb__menu-container p {
  margin: 1rem 0;
}

@media screen and (max-width: 500px){
  .nb__links-container {
    display: none;
  }
  .nb__menu {
    display: flex;
  }
}